import React, { useCallback, useEffect, useRef, useState } from 'react';
import {
  AnswerRow,
  ChangeButton,
  Container,
  InputArea,
  Main,
  PageTitle,
  QuestionRow,
  RowArea,
} from './styles';
import { useNavigate } from 'react-router-dom';
import { api } from '../../api';
import { KeywordType, PerfumeType } from '../../common/types';
import useApi from '../../hooks/useApi';
import { resetUserType } from '../../stores/useUserAuthority';
import Header from '../../common/Header';
import Search from '../../common/Search';

const MyPage = () => {
  const navigate = useNavigate();

  const {
    data: checkToken,
    loading: checkTokenLoading,
    error: checkTokenError,
    fetchApi: fetchCheckToken,
  } = useApi<any>();

  const {
    data: changePasswordData,
    loading: changePasswordLoading,
    error: changePasswordError,
    fetchApi: fetchChangePassword,
  } = useApi<any>();

  const [myToken, setMyToken] = useState<string | null>();
  const [currentPassword, setCurrentPassword] = useState<string>('');
  const [newPassword, setNewPassword] = useState<string>('');
  const [newPassword2, setNewPassword2] = useState<string>('');
  const token = localStorage.getItem('my-token');

  const goToLogin = () => {
    alert(' .');
    navigate('/login');
  };

  /*
   *    api .
   * @author 
   */
  const validateToken = useCallback(async () => {
    if (token && token.length > 0) {
      const res = await fetchCheckToken('post', '/token-validation', {});
      if (res?.length > 0) {
        setMyToken(token);
      } else if (checkTokenError) {
        goToLogin();
      }
    } else {
      goToLogin();
    }
  }, [token]);

  useEffect(() => {
    validateToken();
  }, [validateToken]);

  const handleOnKeyPress = (e: { key: string }) => {
    if (e.key === 'Enter') {
      handleChangePassword();
    }
  };

  const handleChangePassword = async () => {
    if (
      currentPassword.length > 0 &&
      newPassword.length > 0 &&
      newPassword2.length > 0
    ) {
      if (newPassword !== newPassword2) {
        alert('      .');
        return;
      }
      try {
        const data = {
          currentPassword: currentPassword,
          newPassword: newPassword,
        };
        const res = await fetchChangePassword('put', '/password', data);

        if (!res) {
          alert('   ');
        } else {
          if (res === '  .') {
            alert(res);
          } else if (res === ' .') {
            alert(res);
            navigate('/mypage');
          } else {
            alert(res);
          }
        }
      } catch {
        alert('   ');
      }
    } else {
      alert('  .');
    }
  };

  return (
    <Container>
      <Header />
      <Search />

      <Main>
        <PageTitle> </PageTitle>
        <InputArea>
          <RowArea>
            <QuestionRow> </QuestionRow>
            <AnswerRow
              type="password"
              placeholder="  ."
              onChange={(e: {
                target: { value: React.SetStateAction<string> };
              }) => setCurrentPassword(e.target.value)}
              onKeyPress={handleOnKeyPress}
            />
          </RowArea>
          <RowArea>
            <QuestionRow> </QuestionRow>
            <AnswerRow
              type="password"
              placeholder="  ."
              onChange={(e: {
                target: { value: React.SetStateAction<string> };
              }) => setNewPassword(e.target.value)}
              onKeyPress={handleOnKeyPress}
            />
          </RowArea>
          <RowArea>
            <QuestionRow>  </QuestionRow>
            <AnswerRow
              type="password"
              placeholder="   ."
              onChange={(e: {
                target: { value: React.SetStateAction<string> };
              }) => setNewPassword2(e.target.value)}
              onKeyPress={handleOnKeyPress}
            />
          </RowArea>
        </InputArea>

        <ChangeButton onClick={handleChangePassword}></ChangeButton>
      </Main>
    </Container>
  );
};

export default MyPage;
