// @ts-ignore
import React, { useCallback, useRef } from 'react';
import { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import Header from '../../common/Header/index';
import useFindPassword from '../../hooks/auth/useFindPassword';
import useApi from '../../hooks/useApi';
import useUserTypeStore from '../../stores/useUserAuthority';

import {
  Content,
  ContentLogo,
  LoginArea,
  Notice,
  RowArea,
  QuestionRow,
  AnswerRow,
  AlertMessage,
  LoginButton,
  JoinArea,
  JoinTitle,
  JoinButton,
} from './styles';

const FindPassword = () => {
  const { setUserType, userType } = useUserTypeStore(); // Zustand setUserType 

  const {
    data: login,
    loading: loginLoading,
    error: loginError,
    fetchApi: fetchLogin,
  } = useApi<any>();
  const { fetchApi: fetchUserType } = useApi<any>();

  const navigate = useNavigate();
  const [email, setEmail] = useState<string>('');
  const [isEmailValid, setIsEmailValid] = useState<boolean>(true);
  const [emailMessage, setEmailMessage] = useState<string>('');

  const goToHome = () => {
    navigate('/');
  };

  const onChangeEmail = (e: { target: { value: string } }) => {
    const currentEmail = e.target.value;
    setEmail(currentEmail);
    const emailRegularExpression = new RegExp(
      '[0-9a-zA-Z]([-_\\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\\.]?[0-9a-zA-Z])*\\.[a-zA-Z]{2,3}$'
    );
    if (e.target.value.length === 0) {
      setEmailMessage(' ');
      setIsEmailValid(false);
    } else {
      if (!emailRegularExpression.test(currentEmail)) {
        setEmailMessage('    .');
        setIsEmailValid(false);
      } else {
        setEmailMessage('');
        setIsEmailValid(true);
      }
    }
  };

  const { findPassword } = useFindPassword({
    email,
  });

  const handleFindPassword = () => {
    if (isEmailValid) {
      findPassword();
    } else {
      alert('  ');
    }
  };

  const handleOnKeyPress = (e: { key: string }) => {
    if (e.key === 'Enter') {
      handleFindPassword();
    }
  };

  const fetchUserTypeData = async () => {
    const userTypeData = await fetchUserType('get', `/user/${email}`);
    if (userTypeData && userTypeData.authorityDtoSet) {
      const authorityName = userTypeData.authorityDtoSet[0].authorityName;
      setUserType(authorityName);
    }
    goToHome();
  };

  useEffect(() => {
    if (login?.token) {
      localStorage.setItem('my-token', login.token);
      fetchUserTypeData();
    }

    if (loginError) {
      alert(' .');
    }
  }, [login?.token, loginError]);

  return (
    <>
      <Header />
      <Content>
        <ContentLogo>
          <div className="logo__kr"></div>
          <div className="logo__en">Scenchive</div>
        </ContentLogo>

        <LoginArea>
          <Notice>
             
            <br />
              .
            <br />   .
          </Notice>
          <RowArea>
            <QuestionRow></QuestionRow>
            <AnswerRow
              type="text"
              placeholder=" ."
              onChange={onChangeEmail}
              onKeyPress={handleOnKeyPress}
            />
            <AlertMessage>{emailMessage}</AlertMessage>
          </RowArea>
        </LoginArea>

        <LoginButton onClick={handleFindPassword}></LoginButton>

        <JoinArea>
          <JoinTitle>   ?</JoinTitle>
          <JoinButton onClick={() => navigate('/signup')}></JoinButton>
        </JoinArea>
      </Content>
    </>
  );
};

export default FindPassword;
